<template>
    <div class="my-orders">
        <transition name="fade">
            <div v-show="dataLoading" class="loading-overlay">
                <loader></loader>
            </div>
        </transition>
        <div class="wtw-container">
            <div class="back-to-main-menu" @click.click="goToMainMenu">
                <span class="stay-black">Back</span>
            </div>
            <ul class="my-orders" v-if="orders.length">
                <li
                    class="order"
                    v-for="order in orders"
                    :key="order.transaction_id"
                >
                    <ul class="order-items" :if="order.items">
                        <li
                            class="order-item"
                            v-for="item in order.items"
                            :key="item.transaction_item_id"
                        >
                            <div class="item-image">
                                <img :src="item.product_image" />
                            </div>
                            <div class="item-details">
                                <div class="item-brand">
                                    {{ item.product_brand_title }}
                                </div>
                                <div class="item-name">
                                    {{ item.transaction_item_description }}
                                </div>
                                <div class="item-price">
                                    {{ item.transaction_item_price | currency }}
                                </div>
                                <ul class="item-options">
                                    <li v-for="option in item.options">
                                        <span class="option-label"
                                            >{{ option.label }}:</span
                                        >
                                        <span class="option-value">{{
                                            option.value
                                        }}</span>
                                    </li>
                                    <li>
                                        <span class="option-label">QTY:</span>
                                        <span class="option-value">{{
                                            item.transaction_item_quantity
                                        }}</span>
                                    </li>
                                </ul>
                                <div class="item-date">
                                    Order Date: {{ order.transaction_date }}
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <div class="no-results-message" v-if="!orders.length">
            You currently have no existing orders.
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import Mixins from "../../../mixins/mixins";

import Loader from "../../common/loader.vue";

export default {
    mixins: [Mixins],
    components: {
        Loader,
    },
    computed: {
        ...mapGetters({
            orders: "transactions/getTransactions",
        }),
    },
    data() {
        return {
            dataLoading: false,
        };
    },
    methods: {
        goToMainMenu() {
            this.$store.commit({
                type: "menu/setCurrentMenuItem",
                item: {
                    menuContent: "main-menu",
                    component: "main-menu/main-menu.vue",
                    isMenuExpanded: false,
                },
            });
        },
        handleItemClick(item) {
            this.$store.commit({
                type: "menu/setCurrentMenuItem",
                item: {
                    menuContent: "style-setting",
                    component: "style-settings/style-setting.vue",
                    token: item.token,
                    isMenuExpanded: true,
                },
            });
        },
    },
    mounted() {
        this.dataLoading = true;
        this.$store
            .dispatch({
                type: "transactions/fetchTransactions",
            })
            .then(() => {
                this.dataLoading = false;
            })
            .catch(() => {
                this.dataLoading = false;
            });
    },
};
</script>

